.notFoundPage_section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.notFoundPage_section img {
  display: flex;
  width: 1440px;
  height: 625px;
}

@media (max-width: 1458px) {
  .notFoundPage_section img {
    width: 625px;
    height: 625px;
    object-fit: cover;
  }
}
@media (max-width: 650px) {
  .notFoundPage_section img {
    width: 350px;
    height: 350px;
  }
}
